/*| Navigation |*/

nav {
  /* position: fixed; */
  display: flex;
  flex-wrap: wrap;
  height:0vh;
  /* height: auto; */
  font-size: 1.3em;
  top: 0px;
  left: 0;
  width: 100%;
  background: #fff;
  box-shadow: 0 3px 10px -2px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.links{
  
  font-weight: bolder;
  margin-bottom: 2vh;
}
ul {
  list-style: none;
  text-align: center;
  position: relative;
  /* float: right; */
  /* I removed this to remove the  overall shadow */
  /* margin-right: 100px; */
  display: inline-table;
  z-index: 2;
}
ul li {
  /* float: left; */
  transition: all 0.2s ease-in-out;
}

ul li:hover {
  background: rgba(0, 0, 0, 0.15);
  text-decoration: none;
}
ul li:hover > ul {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  text-decoration: none;
  padding-right: .5vw;
  padding-top: 1vh;
  padding-bottom: 1vh;
  padding-left:1vh;
  /* position: relative; */
  /* right: 10px; */
}
ul li {
  float: left;
  transition: all 0.2s ease-in-out;
}
ul li a {
  display: block;
  padding: 30px 20px;
  color: #222;
  text-align: center;
  font-size: 0.9em;
  letter-spacing: 1px;
  text-decoration: none;
  text-transform: uppercase;
}
ul ul {
  display: none;
  background: #fff;
  position: absolute;
  /* top: 100%; */
  box-shadow: -3px 3px 10px -2px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(0, 0, 0, 0.1);
}

ul ul li {
  float: none;
  width: 100%;

  /* display: none; */
  position: relative;
}
ul ul li a {
  padding: 15px 30px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}
ul ul ul {
  position: absolute;
  left: 100%;
  top: 0;
}
